<template>
    <div class="flex flex-wrap -mx-2">
        <div class="w-full lg:w-2/3 xl:w-3/4 mb-4 px-4">
            <h3 class="mb-5">Зарегистрированные</h3>

            <vx-card style="overflow: hidden" v-if="!$store.state.bagholders.list.length">

                <div class="not-bagholders">
                    <div class="icon">
                        <img src="../../assets/images/reg_bh.svg" alt="">
                    </div>

                    <div class="message">
                        У вас нет зарегистрированных бэгхолдеров
                    </div>

                    <div class="sub-message">
                        Чтобы отслеживать работу бэгхолдеров, их необходимо зарегистрировать
                    </div>

                </div>

            </vx-card>

            <vx-card v-else class="mb-4 pointer" v-for="i in $store.state.bagholders.list" @click="$router.push('bagholders/' + i.id)">
                <div slot="no-body">
                    <div class="item">
                        <div class="label">
                            {{ i.name }}
                        </div>
<!--                        <div class="status">-->
<!--                            <div class="chip" :class="{error: !$store.getters['bagholders/isOnline'](i.id)}">-->
<!--                                {{ $store.getters['bagholders/isOnline'](i.id) ? 'Online' : 'Offline' }}-->
<!--                            </div>-->
<!--                        </div>-->
                        <div class="actions">
                            <div @click.self.stop="edit(i.id, i.name)">
                                <vs-icon icon-pack="feather" icon="icon-edit" size="18px" @click.native.self.stop="edit(i.id, i.name)"></vs-icon>
                            </div>
                            <div @click.self.stop="remove(i.id, i.name)">
                                <vs-icon icon-pack="feather" icon="icon-trash-2" size="18px" @click.native.self.stop="remove(i.id, i.name)"></vs-icon>
                            </div>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
        <div class="w-full sm:w-1/1 md:w-full lg:w-1/3 xl:w-1/4 mb-4 px-4">
            <h3 class="mb-5">Незарегистрированные</h3>

            <vx-card style="overflow: hidden" v-if="!$store.getters['bagholders/newList'].length">

                <div class="not-bagholders">
                    <div class="icon">
                        <img src="../../assets/images/unreg_bh.svg" alt="">
                    </div>

                    <div class="message">
                        Бэгхолдеры не найдены
                    </div>

                    <div class="sub-message">
                        В сети не найдены незарегистрированные бэгхолдеры
                    </div>
                </div>

            </vx-card>

            <vx-card class="mb-4" v-else v-for="i in $store.getters['bagholders/newList']">
                <div slot="no-body">
                    <div class="item">
                        <div class="label">
                            {{ i }}
                        </div>
                        <div class="actions">
                            <vs-button radius size="small" color="primary" type="filled" icon-pack="feather" icon="icon-plus" @click="register(i)"></vs-button>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>

        <vs-prompt
            title="Зарегистрировать бэгхолдер"
            accept-text="Зарегистрировать"
            cancel-text="Отмена"
            @cancel="cancelRegister"
            @accept="acceptRegister"
            @close="cancelRegister"
            :active.sync="activeRegister">
            <div class="con-exemple-prompt">
                <div class="mb-1">Введите название</div>
                <vs-input autofocus class="w-full" v-model="name" @keyup.enter="acceptRegister"/>
            </div>
        </vs-prompt>

        <vs-prompt
            title="Переименовать бэгхолдер"
            accept-text="Переименовать"
            cancel-text="Отмена"
            @cancel="cancelEdit"
            @accept="acceptEdit"
            @close="cancelEdit"
            :active.sync="activeEdit">
            <div class="con-exemple-prompt">
                <div class="mb-1">Введите название</div>
                <vs-input autofocus class="w-full" v-model="name" @keyup.enter="acceptEdit"/>
            </div>
        </vs-prompt>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                activeEdit: false,
                activeRegister: false,
                name: '',
                currentMac: ''
            }
        },
        methods: {
            register(mac){
                this.activeRegister = true;
                this.currentMac = mac
            },
            async acceptRegister(){
                try{
                    await this.$http.post('/bagholders', {id: this.currentMac, name: this.name})
                }catch (e) {
                    console.log(e)
                }
                this.cancelRegister()
            },
            cancelRegister(){
                this.activeRegister = false;
                this.currentMac = '';
                this.name = '';
            },
            remove(id, name){
                this.$vs.dialog({
                    type: 'confirm',
                    color: 'danger',
                    title: `Подтвердите действие`,
                    text: 'Вы собираетесь удалить бэгхолдер "'+ name +'"! Все настройки бэгхолдера будут удалены!',
                    acceptText: "Удалить",
                    cancelText: "Отмена",
                    accept: this.acceptRemove(id)
                })
            },
            acceptRemove: function(id) {
                return async () => {
                    try{
                        await this.$http.delete('/bagholders/' + id)
                    }catch (e) {
                        console.log(e)
                    }
                }
            },
            edit(id, name){
                this.name = name;
                this.currentMac = id;
                this.activeEdit = true;
            },
            async acceptEdit(){
                try{
                    await this.$http.post('/bagholders/' + this.currentMac, {name: this.name})
                }catch (e) {
                    console.log(e)
                }
                this.cancelEdit()
            },
            cancelEdit(){
                this.activeEdit = false;
                this.currentMac = '';
                this.name = '';
            }
        }
    }
</script>

